<template><section><html><head></head><body><h1>TiandituImageryProvider</h1>
<p>The <code v-pre="">vc-provider-imagery-tianditu</code> component is used to load WMTS image service of <a href="http://lbs.tianditu.gov.cn/home.html">Tianditu</a>.</p>
<h2>Example</h2>
<h3>Load an imagerylayer with TiandituImageryProvider</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery ref="layerText" :alpha="alpha" :brightness="brightness" :contrast="contrast" :sort-order="20">
          <vc-provider-imagery-tianditu map-style="eva_c" token="436ce7e50d27eede2f2929307e6b33c0">
          </vc-provider-imagery-tianditu>
        </vc-layer-imagery>
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast" :sort-order="10">
          <vc-provider-imagery-tianditu :map-style="mapStyle" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>alpha</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>brightness</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>contrast</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>switch</span>
        <md-select v-model="mapStyle" placeholder="switch mapStyle">
          <md-option v-for="item in options" :key="item.value" :value="item.value">
            {{item.label}}
          </md-option>
        </md-select>
      </div>
    </div>
  </template>
  
