<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>mapStyle</td>
<td>String</td>
<td><code v-pre="">'img_w'</code></td>
<td><code v-pre="">optional</code> The type of service.</td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td><code v-pre="">'天地图全球影像服务'</code></td>
<td><code v-pre="">optional</code> The credit of service</td>
</tr>
<tr>
<td>token</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> The key of service. <a href="http://lbs.tianditu.gov.cn/home.html">Application address</a></td>
</tr>
<tr>
<td>protocol</td>
<td>String</td>
<td><code v-pre="">https</code></td>
<td><code v-pre="">optional</code> The network protocol of service, can be <code v-pre="">https</code> or <code v-pre="">http</code></td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code>The minimum tile level to request, or undefined if there is no minimum. This parameter is ignored when accessing a tiled server.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td><code v-pre="">20</code></td>
<td><code v-pre="">optional</code>The maximum tile level to request, or undefined if there is no maximum. This parameter is ignored when accessing a tiled server.</td>
</tr>
<tr>
<td>projectionTransforms</td>
<td>Boolean|Object</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> Specify the projection transformation parameters. <strong>structure: { from: 'GCJ02', to: 'WGS84' }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>
<p>mapStyle can take values:</p>
<ul>
<li>'cia_c': Global Chinese Annotation Service(GeographicTilingScheme).</li>
<li>'cia_w': Global Chinese Annotation Service(WebMercatorTilingScheme).</li>
<li>'cta_c': Global Terrain Chinese Annotation Service(GeographicTilingScheme).</li>
<li>'cta_w': Global Terrain Chinese Annotation Service(WebMercatorTilingScheme).</li>
<li>'cva_c': Global Vector Chinese Annotation Service(GeographicTilingScheme).</li>
<li>'cva_w': Global Vector Chinese Annotation Service(WebMercatorTilingScheme).</li>
<li>'ela_c': Global Image English Annotation Service(GeographicTilingScheme).</li>
<li>'ela_w': Global Image English Annotation Service(WebMercatorTilingScheme).</li>
<li>'eva_c': Global Vector English Annotation Service(GeographicTilingScheme).</li>
<li>'eva_w': Global Vector English Annotation Service(WebMercatorTilingScheme).</li>
<li>'img_c': Global Image Map Service(GeographicTilingScheme).</li>
<li>'img_w': Global Image Map Service(WebMercatorTilingScheme).</li>
<li>'ter_c': Global Terrain Shading Service(GeographicTilingScheme).</li>
<li>'ter_w': Global Terrain Shading Service(WebMercatorTilingScheme).</li>
<li>'vec_c': Global vector map service(GeographicTilingScheme).</li>
<li>'vec_w': Global vector map service(WebMercatorTilingScheme).</li>
</ul>
</li>
<li>
<p>Refer to the official document of SuperMap: <strong><a href="http://support.supermap.com.cn:8090/webgl/Build/Documentation/SuperMapImageryProvider.html">SuperMapImageryProvider</a></strong></p>
</li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>Triggers when SuperMapImageryProvider is ready. It returns a core class of Cesium, a viewer instance.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Gets an event that is raised when the imagery provider encounters an asynchronous error.. By subscribing to the event, you will be notified of the error and can potentially recover from it. Event listeners are passed an instance of TileProviderError.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>Gets a promise that resolves to true when the provider is ready for use.</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          protocol: 'http',
          options: [
            {
              value: 'img_c',
              label: 'Global Image Map Service(GeographicTilingScheme)'
            },
            {
              value: 'img_w',
              label: 'Global Image Map Service(WebMercatorTilingScheme)'
            },
            {
              value: 'vec_c',
              label: 'Global vector map service(GeographicTilingScheme)'
            },
            {
              value: 'vec_w',
              label: 'Global vector map service(WebMercatorTilingScheme)'
            },
            {
              value: 'ter_c',
              label: 'Global Terrain Shading Service(GeographicTilingScheme)'
            },
            {
              value: 'ter_w',
              label: 'Global Terrain Shading Service(WebMercatorTilingScheme)'
            }
          ],
          mapStyle: 'img_c',
          alpha: 1,
          brightness: 1,
          contrast: 1
        }
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.Cesium = Cesium
          this.viewer = viewer
        },
        layerAdded() {
          if (this.$refs.layerText.imageryLayer) {
            const { viewer } = this
            viewer.imageryLayers.raiseToTop(this.$refs.layerText.imageryLayer)
          }
        }
      }
    }
  </script>